.menu-nav {
    background-color: #333;
    padding: 10px 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;

    .menu-list {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        gap: 15px;
        list-style-type: none;
        padding: 0;
        margin: 0;
        justify-content: center; 
        align-items: center;
    }

    .menu-item {
        padding: 0;
        width: auto;
        .menu-link {
            color: #fff;
            text-decoration: none;
            padding: 8px 15px;
            border-radius: 4px;
            font-size: 16px;
            transition: background-color 0.3s;

            &:hover {
                background-color: #555;
            }
        }
    }
}
